import React, { useState } from 'react';
import { Prettify } from '../model/common';
import { Button } from './Button';
import { TabsContentProps, TabsListProps, TabsProps, TabsTriggerProps } from '../model/tab';

export const Tabs = (props: TabsProps) => {
  const [activeTab, setActiveTab] = useState(props?.defaultValue);

  const handleTabClick = (value: string) => {
    setActiveTab(value);
  };

  const clonedChildren = React.Children.map(props?.children, (child) => {
    if (React.isValidElement(child)) {
      if (child.type === Trigger) {
        //@ts-ignore
        return React.cloneElement(child, { activeTab, onClick: handleTabClick });
      }
    }
    return child;
  });

  return <section className={props?.className}>{clonedChildren}</section>;
};

export const List = (props: Prettify<TabsListProps>) => (
  <div className={props?.className} aria-label={props?.ariaLabel}>
    {props?.children}
  </div>
);

export const Trigger = (props: Prettify<TabsTriggerProps>) => (
  <Button
    type='button'
    className={props?.value === props?.activeTab ? props?.className + ' ' + props?.activeClass : props?.className}
    onClick={() => props?.onClick(props?.value)} // Use the onClick prop
  >
    {props?.children}
  </Button>
);

export const Content = (props: Prettify<TabsContentProps>) => (
  <div className={props?.className} style={{ display: props?.value === props?.activeTab ? 'block' : 'none' }}>
    {props?.children}
  </div>
);
Tabs.List = List;
Tabs.Trigger = Trigger;
Tabs.Content = Content;
