import React from 'react';
import { Prettify } from '../model/common';
import { TimeProps, TimelineItemProps } from '../model/timeline';
import { Clsx } from '../helpers/clsx';

export const TimelineItem = (props: Prettify<TimelineItemProps>) => {
  return (
    <div className={Clsx('timeline-item', `timeline-item-${props.position ? props.position : 'left'}`, props.className ? props.className : '')}>
      {props.label ? <div className='timeline-item-label'>{props.label}</div> : null}
      <div className='timeline-item-tail'></div>
      {props.dot ? props.dot : <div className={Clsx('timeline-item-head', `timeline-item-head-${props.color ? props.color : 'gray'}`)}></div>}
      <div className='timeline-item-content'>{props.children}</div>
    </div>
  );
};

export const Timeline = (props: Prettify<TimeProps>) => <div className={props?.className}>{props?.children}</div>;

Timeline.Item = TimelineItem;
