import React from 'react';
import { TooltipContentProps, TooltipContextType, TooltipPortalProps, TooltipProviderProps, TooltipTriggerProps } from '../model/tooltip';

const TooltipContext = React.createContext<TooltipContextType | undefined>(undefined);
export const TooltipProvider = ({ children }: TooltipProviderProps) => {
  const [isVisible, setIsVisible] = React.useState(false);
  const showTooltip = () => {
    setIsVisible(true);
  };
  const hideTooltip = () => {
    setIsVisible(false);
  };
  return <TooltipContext.Provider value={{ isVisible, showTooltip, hideTooltip }}>{children}</TooltipContext.Provider>;
};

export const TooltipTrigger: React.FC<TooltipTriggerProps> = ({ asChild, children, className }) => {
  const { showTooltip, hideTooltip }: any = React.useContext(TooltipContext);
  const handleMouseEnter = () => {
    if (asChild) {
      showTooltip();
    }
  };
  const handleMouseLeave = () => {
    if (asChild) {
      hideTooltip();
    }
  };
  return (
    <div className={className} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      {children}
    </div>
  );
};

export const TooltipPortal: React.FC<TooltipPortalProps> = ({ children }) => {
  const { isVisible }: any = React.useContext(TooltipContext);
  if (isVisible) {
    return <div className='TooltipPortal'>{children}</div>;
  }
  return null;
};

export const TooltipContent: React.FC<TooltipContentProps> = ({ children, sideOffset }) => {
  return (
    <div className='TooltipContent' style={{ marginLeft: sideOffset }}>
      {children}
    </div>
  );
};

TooltipProvider.TooltipTrigger = TooltipTrigger;
TooltipProvider.TooltipPortal = TooltipPortal;
TooltipProvider.TooltipContent = TooltipContent;
