import React from 'react';
import { Props } from '../model/table';
function TableWrapper<RecordType>(props: Props<RecordType>) {
  const { showHeader = true } = props;
  return (
    <>
      {props?.title}
      {props.dataSource.length ? (
        <table className={props.className}>
          {showHeader ? (
            <thead>
              <tr>
                {props.columns.map((column, i) => (
                  <th key={`columnId-${i}`}>{column.title}</th>
                ))}
              </tr>
            </thead>
          ) : null}
          <tbody>
            {props.dataSource.map((row, rowIndex) => (
              <tr key={`rowId-${rowIndex}`}>
                {props.columns.map((column, colIndex) => (
                  <td key={colIndex} className={column.className} width={column.width} colSpan={column.colSpan}>
                    {column.render(row, rowIndex)}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      ) : null}
    </>
  );
}
export const Table = TableWrapper;
