import React from 'react';
import { button, link } from '../model/button';

export const Button = (props: button | link) => {
  switch (props.type) {
    case 'link':
      return (
        <a className={props.className} href={props.href}>
          {props.children}
        </a>
      );

    default:
      return <button {...props}>{props.children}</button>;
  }
};
