type Primitive = string | number;

type ObjectCondition = {
  [key: string]: boolean | undefined;
};

type ClassNamesInput = Primitive | ObjectCondition | (Primitive | ObjectCondition)[] | undefined;
function condition(className: ClassNamesInput): string {
  let cls = '';

  if (typeof className === 'string' || typeof className === 'number') {
    cls += className;
  } else if (typeof className === 'object') {
    if (Array.isArray(className)) {
      for (const item of className) {
        if (item) {
          const t = condition(item);
          if (t) {
            if (cls) {
              cls += ' ';
            }
            cls += t;
          }
        }
      }
    } else {
      for (const key in className) {
        if (className[key]) {
          if (cls) {
            cls += ' ';
          }
          cls += key;
        }
      }
    }
  }

  return cls;
}
export const Clsx = (...args: ClassNamesInput[]): string => {
  let cls = '';
  for (const arg of args) {
    if (arg) {
      const clss = condition(arg);
      if (clss) {
        if (cls) {
          cls += ' ';
        }
        cls += clss;
      }
    }
  }

  return cls;
};
