import React from 'react';
import ReactDOM from 'react-dom';
import { Button } from './Button';
import CloseIcon from './icons/CloseIcon';
import { ModalProps } from '../model/modal';
import { Clsx } from '../helpers/clsx';

// type ModalActionProps = React.ButtonHTMLAttributes<HTMLButtonElement>;
let modalRoot: HTMLDivElement | null = null;
const ModalComponents = (props: ModalProps) => {
  const { title, maskClosable = false, okButton, cancelButton, className, children } = props;
  return (
    <div className='modal-wrapper'>
      <div className={Clsx('modal-content', className ? className : '')}>
        <Button onClick={cancelButton?.onClick} type='button' className='modal-close-icon'>
          {props.closeIcon ? props.closeIcon : <CloseIcon />}
        </Button>
        {title ? <div className='modal-header'>{title}</div> : null}
        <div className='modal-body'>{children}</div>
        <div className='modal-actions'>
          <Button type={cancelButton?.type!} className={Clsx('model-btn model-btn-default', cancelButton?.className)} onClick={cancelButton?.onClick} disabled={cancelButton?.disabled}>
            {cancelButton?.children}
          </Button>
          <Button type={okButton?.type!} className={Clsx('model-btn model-btn-primary', okButton?.className)} onClick={okButton?.onClick} disabled={okButton?.disabled}>
            {okButton?.children}
          </Button>
        </div>
      </div>
      {maskClosable ? <Button onClick={cancelButton?.onClick} type='button' className='modal-mask'></Button> : <div className='modal-mask'></div>}
    </div>
  );
};
export const Modal = (props: ModalProps) => {
  if (typeof window === 'undefined') return;
  if (!modalRoot && props.visible) {
    modalRoot = document.createElement('div');
    document.body.appendChild(modalRoot);
  }
  return props.visible ? ReactDOM.createPortal(<ModalComponents {...props} visible={props.visible} />, modalRoot as Element) : null;
};
export default Modal;
