import React from 'react';
import { Props } from '../model/select';

export function Select<T>(props: Props<T>) {
  const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedValue = event.target.value as T | null;
    const selectedOption = props?.options.find((option) => option.value === selectedValue) || null;
    props?.onChange(selectedOption ? selectedOption.value : null);
  };

  return (
    <select value={props?.value || ('' as any)} onChange={handleChange}>
      <option value=''>Select an option</option>
      {props?.options.map((option) => (
        <option key={option.value as any} value={option.value as any}>
          {option.label as string}
        </option>
      ))}
    </select>
  );
}
