import { Children, isValidElement } from 'react';
import { CaseProps, Props } from '../model/condition';

export const Switch = (props: Props) => {
  let matchChild: React.ReactNode | null = null;
  let defaultChild: React.ReactNode | null = null;

  Children.forEach(props.children, (child) => {
    if (!child) return;
    if (isValidElement(child)) {
      if (!matchChild && child.type === Case) {
        const { condition } = child.props;
        const conditionResult = Boolean(condition);
        if (conditionResult) {
          matchChild = child;
        }
      } else if (!defaultChild && child.type === Default) {
        defaultChild = child;
      }
    }
  });

  return matchChild || defaultChild || null;
};

export const Case = (props: CaseProps) => {
  return props.condition ? props.children : null;
};

export const Default = (props: Props) => {
  return props.children;
};

Switch.Case = Case;
Switch.Default = Default;
