import React from 'react';
import { Button } from './Button';
import { Prettify } from '../model/common';
import { AccordionContentProps, AccordionItemProps, AccordionTriggerProps, Props } from '../model/accordion';

export const Accordion = (props: Prettify<Props>) => {
  return <section className={props?.className}>{props?.children}</section>;
};

export const AccordionItem = (props: Prettify<AccordionItemProps>) => {
  return (
    <div className={props?.className} aria-label={props?.value}>
      {props?.children}
    </div>
  );
};

export const AccordionTrigger = (props: Prettify<AccordionTriggerProps>) => {
  return (
    <Button className={props?.className} type='button' onClick={props?.onClick}>
      {props?.children}
    </Button>
  );
};

export const AccordionContent = (props: Prettify<AccordionContentProps>) => {
  return props?.active ? <div className={props?.className}>{props?.children}</div> : null;
};

Accordion.Item = AccordionItem;
Accordion.Trigger = AccordionTrigger;
Accordion.Content = AccordionContent;
